<script>
import dayjs from "dayjs";
import JobCondition from "./JobCondition.vue";
import MapService from "./MapService.vue";
export default {
  name: "JobDetailBody",
  props: { className: { type: String }, job: { type: Object } },
  components: { JobCondition, MapService },
  data() {
    return {
      map: null,
      isCTT: false,
      mapOptions: {
        lat: 37,
        lng: 127,
        zoom: 10,
        zoomControl: true,
        zoomControlOptions: { position: "TOP_RIGHT" },
        mapTypeControl: true
      }
    };
  },
  methods: {
    onLoad(vue) {
      this.map = vue;
    },
    onWindowLoad(that) {},
    // onMarkerClicked(event) {
    //   this.info = !this.info;
    // },
    // onMarkerLoaded(vue) {
    //   this.marker = vue.marker;
    // },

    getSidoStr(code) {
      // console.log('getSidoStr',code);
      // return this.$props?.getName?.(code, 'SIDO_CD')||'';
      return "서울";
    },
    toShortDate(str) {
      return dayjs(str).format("YY.MM.DD");
    },
    hasJob0(jobCodeStr) {
      // return jobCodeStr.includes('QLF_CD_005')
      // console.log('hasJob0',jobCodeStr);
      return jobCodeStr.indexOf("QLF_CD_005") > -1;
    },
    hasJob1(jobCodeStr) {
      // return jobCodeStr.includes('QLF_CD_001')
      // console.log('hasJob1',jobCodeStr);
      return jobCodeStr.indexOf("QLF_CD_001") > -1;
    },
    hasJob2(jobCodeStr) {
      // return jobCodeStr.includes('QLF_CD_002')
      // console.log('hasJob2',jobCodeStr);
      return jobCodeStr.indexOf("QLF_CD_002") > -1;
    }
  },
  filters: {
    toDateFormat(str) {
      const _date = dayjs(str);
      return _date.format("YY-MM-DD");
    },
    toTelFormat(str) {
      // console.log('toTelFormat', str);
      const _result = /^(0[1-9]{1,2})([0-9]{3,4})([0-9]{4})$/.exec(str);
      return _result ? `${_result[1]}-${_result[2]}-${_result[3]}` : str;
    },
    getPosition(str) {
      // console.log('getPosition', str);
      // return str;
      if (typeof str === "string") {
        const _arr = str.split("|") || [];
        const _result = _arr.map(_pos =>
          _pos === "QLF_CD_005"
            ? '<strong class="sec-indigo4">치과의사</strong>'
            : _pos === "QLF_CD_001"
            ? '<strong class="ter-blue3">치과위생사</strong>'
            : _pos === "QLF_CD_002"
            ? '<strong class="ter-green1">간호조무사</strong>'
            : ""
        );
        console.log(_result);
        return _result.join("");
      } else {
        return "";
      }
    }
  }
};
</script>



