import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default function createStore() {
  return new Vuex.Store({
    state: {
      // todoItems: [
      //   { id: 1, content: "CSR을 만들어보자", activation: true },
      //   { id: 2, content: "CSR 코드 분할", activation: false },
      //   { id: 3, content: "SSR을 만들어보자", activation: false },
      // ],
      commonCodes: [],
      jobList: [],
    },
    getters: {
      sidoCodes(state) {
        return state.commonCodes["SIDO_CD"];
      },
      // sigunguCodes(state) {
      //   return state.commonCodes["SIGUNGU_CD"];
      // },
      jobCodes(state) {
        return state.commonCodes["QLF_CD"];
      },
    },
    mutations: {
      // SET_TODO_ITEMS(state, todoItems) {
      //   state.todoItems = todoItems;
      // },
      SET_JOB_LIST(state, jobList) {
        state.jobList = jobList;
      },
      SET_COMMON_CODES(state, commonCodes) {
        state.commonCodes = commonCodes;
      },
    },
  });
}



