      </div>
      <div class="col-4 posting-item">
        <img
          src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABgAAAAYAgMAAACdGdVrAAAABGdBTUEAALGPC/xhBQAAAAFzUkdCAK7OHOkAAAAJUExURUdwTOyiN+mcGQ9aWH0AAAACdFJOUwDo2EKncgAAADRJREFUCNdjYCAGsE0AUxwKSFTWCq2VQGrVCq1VIAoIMKgVXAxaYArKY8JUkrVq1UpiXAAASrkY4SG5JhQAAAAASUVORK5CYII="
