<style>
* {
  margin: 0;
  padding: 0;
}
.posting-area {
  margin-top: 13px;
  margin-bottom: 3px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  flex-grow: 1;

  background-color: #f6f8fa;
}
.posting-area .row {
  display: flex;
  flex-direction: row;
}
.col-4 {
  width: 33.33333%;
}
</style>

<script>
export default {
    name: 'JobDetailBody',
    props: { condition: null },
    methods: {
    },
    mounted() {
    },
    filters: {
        jobPosition(code){
            switch(code){
                case "QLF_CD_001":
                    return "치과위생사"
                case "QLF_CD_002":
                    return "간호조무사"
                case "QLF_CD_003":
                    return "치과기공사"
                case "QLF_CD_004":
                    return "코디네이터"
                case "QLF_CD_005":
                    return "치과의사"
                case "QLF_CD_999":
                    return "기타"
                default:""
            }
        }
    }
}
</script>



