<script>
const naverClientID = "";
const kakaoClientID = "4b4054a93ee16ebece053620c280f5f2";
export default {
  name: "MapService",
  props: { job: { type: Object } },
  data: () => ({
    map: null,
  }),
  mounted() {
    if (window.naver && window.naver.maps) {
      this.loadMap();
    } else {
      this.loadScript();
    }
  },
  methods: {
    loadScript() {
      const script = document.createElement("script");
      //   script.onload = () => naver.maps.load(this.loadMap);
      //   script.src = `https://openapi.map.naver.com/openapi/v3/maps.js?ncpClientId=${naverClientID}`;
      script.src = `//dapi.kakao.com/v2/maps/sdk.js?appkey=${kakaoClientID}&autoload=false`;
      document.head.appendChild(script);
      script.onload = () => {
        daum.maps.load(this.loadMap);
      };
    },
    loadMap() {
      //   const container = document.getElementById("map");
      //   const mapOptions = {
      //     center: new naver.maps.LatLng(job.latitudeValue, job.longitudeValue),
      //     zoom: 10,
      //   };
      //   this.map = new naver.maps.Map(container, mapOptions);
      debugger;

      var mapContainer = document.getElementById("mapService"), // 지도를 표시할 div
        mapOption = {
          center: new kakao.maps.LatLng(
            this.job.latitudeValue,
            this.job.longitudeValue
          ), // 지도의 중심좌표
          level: 3, // 지도의 확대 레벨
          mapTypeId: kakao.maps.MapTypeId.ROADMAP, // 지도종류
        };

      // 지도를 생성한다
      var map = new kakao.maps.Map(mapContainer, mapOption);

      // 마우스 드래그와 모바일 터치를 이용한 지도 이동을 막는다
      map.setDraggable(false);

      // 마우스 휠과 모바일 터치를 이용한 지도 확대, 축소를 막는다
      map.setZoomable(false);

      // 지도에 확대 축소 컨트롤을 생성한다
      var zoomControl = new kakao.maps.ZoomControl();

      // 지도의 우측에 확대 축소 컨트롤을 추가한다
      map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);

      // 지도에 마커를 생성하고 표시한다
      var marker = new kakao.maps.Marker({
        position: new kakao.maps.LatLng(
          this.job.latitudeValue,
          this.job.longitudeValue
        ), // 마커의 좌표
        map: map, // 마커를 표시할 지도 객체
      });
    },
    // getInfo() {
    //     // 지도의 현재 중심좌표를 얻어옵니다
    //     var center = map.getCenter();

    //     // 지도의 현재 레벨을 얻어옵니다
    //     var level = map.getLevel();

    //     // 지도타입을 얻어옵니다
    //     var mapTypeId = map.getMapTypeId();

    //     // 지도의 현재 영역을 얻어옵니다
    //     var bounds = map.getBounds();

    //     // 영역의 남서쪽 좌표를 얻어옵니다
    //     var swLatLng = bounds.getSouthWest();

    //     // 영역의 북동쪽 좌표를 얻어옵니다
    //     var neLatLng = bounds.getNorthEast();

    //     // 영역정보를 문자열로 얻어옵니다. ((남,서), (북,동)) 형식입니다
    //     var boundsStr = bounds.toString();

    //     var message = '지도 중심좌표는 위도 ' + center.getLat() + ', <br>';
    //     message += '경도 ' + center.getLng() + ' 이고 <br>';
    //     message += '지도 레벨은 ' + level + ' 입니다 <br> <br>';
    //     message += '지도 타입은 ' + mapTypeId + ' 이고 <br> ';
    //     message += '지도의 남서쪽 좌표는 ' + swLatLng.getLat() + ', ' + swLatLng.getLng() + ' 이고 <br>';
    //     message += '북동쪽 좌표는 ' + neLatLng.getLat() + ', ' + neLatLng.getLng() + ' 입니다';

    //     // 개발자도구를 통해 직접 message 내용을 확인해 보세요.
    //     // ex) console.log(message);
    // }
  },
};
</script>
<template>
  <div id="mapService" ref="map"></div>
</template>

<style>
#mapService {
  border: solid 1px #999;
  width: 100%;
  height: 50vh;
  margin: 12px 0;
}
</style>



