import Vue from "vue";
import Router from "vue-router";
import Home from "../views/Home.vue";
import JobList from "../views/JobList.vue";
import NotFound from "../views/NotFound.vue";

// VueRouter 플러그인을 사용합니다.

Vue.use(Router);

export default new Router({
  mode: "history",
  // base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "Home",
      redirect: "/job-list"
      // component: Home
    },
    {
      path: "/job-list",
      name: "JobList",
      component: JobList
    },
    {
      path: "/job-list/:id/:seq",
      name: "JobDetail",
      component: JobList
    },
    {
      path: "*",
      name: "NotFound",
      component: NotFound
    }
  ]
});



