<template>
  <div
    class="card-body"
    no-body=""
    @mouseenter="() => (this.onHover = true)"
    @mouseover="() => (this.onHover = true)"
    @mouseleave="() => (this.onHover = false)"
    @mouseout="() => (this.onHover = false)"
  >
    <span
      v-if="job.topYesOrNo === 'Y'"
      class="badge bg-secondary b-posting-badge top"
      >TOP</span
    >
    <span
      v-else-if="job.newYesOrNo === 'Y'"
      class="badge bg-secondary b-posting-badge new"
      >NEW</span
    >
    <!-- {{ JSON.stringify(job) }} -->
    <div class="logo min" v-if="job.fileId">
      <img
        :src="`https://job.denall.com/txm/file/download?fileId=${job.fileId}`"
        :title="job.customerName"
        :alt="job.customerName"
      />
    </div>
    <div v-else-if="job.customerName" class="logo no" :title="job.customerName">
      {{ job.customerName }}
    </div>
    <div v-show="onHover" class="info">{{ job.hireAnnounceName }}</div>
    <div class="date">
      <small class="ter-blue2">
        <i class="bi bi-geo-alt-fill"></i>
      </small>
      {{ sidoStr }}
      <div class="vr"></div>
      {{ Number.isNaN(job.decimalDay) ? "모집시까지" : job.recruitEndDate | limitWord }}
    </div>
    <div v-if="job.qualification" class="status">
      <strong v-if="hasJob4(job.qualification)" class="sec-indigo4"
        >치과의사</strong
      >
      <strong v-if="hasJob1(job.qualification)" class="ter-blue3"
        >치과위생사</strong
      >
      <strong v-if="hasJob2(job.qualification)" class="ter-green1"
        >간호조무사</strong
      >
      <strong v-if="hasJob3(job.qualification)" class="sec-pink4"
        >치과기공사</strong
      >
      <strong v-if="hasJob5(job.qualification)" class="ter-cyan1"
        >코디네이터</strong
      >
      <strong v-if="hasJob9(job.qualification)" class="ter-brown2">기타</strong>
    </div>
  </div>
</template>
<style>
* {
  margin: 0;
  padding: 0;
}
.card-body {
  display: flex;
  flex-direction: column;
  align-content: center;
}
.card-body:hover {
  gap: 20px;
}
.card-body > .status {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2px;
}
.card-body > * {
  font-size: 13px;
  text-align: center;
  text-decoration: none;
}
.b-posting-badge.new {
  color: #ff438e;
}
.b-posting-badge.top {
  color: #ef7c1c;
}
.job-offer-conts * {
  letter-spacing: -0.3px;
}
.b-posting-badge {
  position: absolute;
  right: 0;
  top: 3px;
  background-color: transparent !important;
  font-size: 11px;
  font-weight: 700;
}
.bg-secondary {
  --bs-bg-opacity: 1;
  background-color: rgba(
    var(--bs-secondary-rgb),
    var(--bs-bg-opacity)
  ) !important;
}
.badge {
  --bs-badge-padding-x: 0.65em;
  --bs-badge-padding-y: 0.35em;
  --bs-badge-font-size: 0.75em;
  --bs-badge-font-weight: 700;
  --bs-badge-color: #fff;
  --bs-badge-border-radius: 0.375rem;
  display: inline-block;
  padding: var(--bs-badge-padding-y) var(--bs-badge-padding-x);
  font-size: var(--bs-badge-font-size);
  font-weight: var(--bs-badge-font-weight);
  line-height: 1;
  color: var(--bs-badge-color);
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: var(--bs-badge-border-radius);
}
.logo.no {
  height: 36px;
  font-size: 20px;
  font-weight: 700;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  word-break: break-all;
}
.logo > img {
  max-width: 100%;
  max-height: 30px;
}
.card-body .info {
  /* margin: 16px 0; */
  min-height: 40px;
  font-size: 14px;
  line-height: 1.43;
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  letter-spacing: -0.3px;
}

.sec-gray-100 {
  color: #f2f4f8 !important;
}

.sec-gray-200 {
  color: #f5f7fa !important;
}

.sec-gray-300,
.sec-gray-400 {
  color: #7a7a7a !important;
}

.sec-gray-500 {
  color: #dadbdb !important;
}

.white {
  color: #fff;
}

.black {
  color: #000;
}

.primary {
  color: #4c54e9;
}

.sec-indigo1 {
  color: #5770ff;
}

.sec-indigo4 {
  color: #883c9f;
}

.sec-indigo5 {
  color: #c61eff;
}

.sec-pink1 {
  color: #ff6089;
}

.sec-pink2 {
  color: #e776a3;
}

.sec-pink3 {
  color: #ff2883;
}

.sec-pink4 {
  color: #e3526e;
}

.ter-blue1 {
  color: #1288fe;
}

.ter-blue2 {
  color: #a3d3ff;
}

.ter-blue3 {
  color: #2368da;
}

.ter-blue4 {
  color: #edf9ff;
}

.ter-blue5 {
  color: #83a9ff;
}

.ter-blue6 {
  color: #2563a2;
}

.ter-blue7 {
  color: #2e3c9b;
}

.ter-teal1 {
  color: #02ab98;
}

.ter-teal3 {
  color: #009d8e;
}

.ter-teal5 {
  color: #1a6891;
}

.ter-brown1 {
  color: #773e3e;
}

.ter-brown2 {
  color: #a86b02;
}

.ter-green1 {
  color: #129269;
}

.ter-green2 {
  color: #2e7c4a;
}

.ter-yellow {
  color: #ffebb2;
}

.ter-red {
  color: #ff3838;
}

.ter-gray1 {
  color: #a0a0a0;
}

.ter-cyan1 {
  color: #009fc5;
}

.font-weight-bold {
  font-weight: 700 !important;
}

.font-weight-semibold {
  font-weight: 600 !important;
}

.font-weight-medium {
  font-weight: 500 !important;
}

.font-weight-rgular {
  font-weight: 400 !important;
}

.font-size-12 {
  font-size: 12px;
}

.font-size-13 {
  font-size: 13px;
}

.font-size-14 {
  font-size: 14px;
}

.font-size-16 {
  font-size: 16px;
}

.font-size-15 {
  font-size: 15px;
}

.font-size-18 {
  font-size: 18px;
}

.font-size-20 {
  font-size: 20px;
}

.font-size-22 {
  font-size: 22px;
}

.font-size-24 {
  font-size: 24px !important;
}

.font-size-28 {
  font-size: 28px;
}
</style>

<script>
// import Vue from 'vue'
import dayjs from 'dayjs'
// import { mapState, mapActions } from 'vuex';
export default {
  name: 'JobCard',
  data () {
    return {
      onHover: false
    }
  },
  props: {job: {type: Object} },
  methods: {
    // getSidoStr(code) {
    //     // console.log('getSidoStr',code);
    //     // return this.$props?.getName?.(code, 'SIDO_CD')||'';
    //     return '서울';
    // },
    toShortDate (str) {
      return dayjs(str).format('YY.MM.DD')
    },
    // 치과위생사
    hasJob1 (jobCodeStr) {
      // return jobCodeStr.includes('QLF_CD_001')
      // console.log('hasJob1',jobCodeStr);
      return jobCodeStr.indexOf('QLF_CD_001') > -1
    },
    // 간호조무사
    hasJob2 (jobCodeStr) {
      // return jobCodeStr.includes('QLF_CD_002')
      // console.log('hasJob2',jobCodeStr);
      return jobCodeStr.indexOf('QLF_CD_002') > -1
    },
    // 치과기공사
    hasJob3 (jobCodeStr) {
      // return jobCodeStr.includes('QLF_CD_003')
      // console.log('hasJob3',jobCodeStr);
      return jobCodeStr.indexOf('QLF_CD_003') > -1
    },
    // 치과의사
    hasJob4 (jobCodeStr) {
      return jobCodeStr.indexOf('QLF_CD_004') > -1
    },
    // 코디네이터
    hasJob5 (jobCodeStr) {
      return jobCodeStr.indexOf('QLF_CD_005') > -1
    },
    // 기타
    hasJob9 (jobCodeStr) {
      return jobCodeStr.indexOf('QLF_CD_999') > -1
    }
  },
  mounted () {
    // if(this.$props.job) {
    //     this.job = this.$props.job;
    // }
    // console.log(this);
  },
  computed: {
    // mapGetters({
    //   doneCount: 'item/doneTodosCount'   // this.doneCount 속성을 this.$store.getters['item/doneTodosCount'] 에 매핑
    // }),
    sidoStr () {
      const _codeMap = this.$store.getters.sidoCodes
      return _codeMap[this.job.sidoCode] || ''
    },
    sigunguStr () {
      const _codeMap = this.$store.getters.sigunguCodes
      return _codeMap[this.job.sigunguCode] || ''
    }
  },
  filters: {
    getPosition (str) {
      // console.log('getPosition', str);
      // return str;
      if (typeof str === 'string') {
        const _arr = str.split('|') || []
        const _result = _arr.map(_pos => (_pos === 'QLF_CD_005' ? '<strong class="sec-indigo4">치과의사</strong>' : _pos === 'QLF_CD_001' ? '<strong class="ter-blue3">치과위생사</strong>' : _pos === 'QLF_CD_002' ? '<strong class="ter-green1">간호조무사</strong>' : ''))
        // console.log(_result);
        return _result.join('')
      } else {
        return ''
      }
    },
    limitWord (str) {
      const _endDate = dayjs(str)
      const _days = _endDate.diff(Date.now(), 'days')
      // console.log('##', {str,_days});
      const _resultArr = [`~ ${_endDate.format('YY-MM-DD')}`]
      if (!Number.isNaN(_days)) {
        if (_days >= 0 && _days <= 7) {
          switch (_days) {
            case 0:
              _resultArr.push(`(D-Day)`)
              break
            default:
              _resultArr.push(`(D-${_days})`)
              break
          }
          return _resultArr.join(' ')
        } else {
          return '모집시까지'
        }
      } else {
        // invalid
        return '모집시까지'
      }
    }
  }
}
</script>



