<template>
  <div class="modal-mask" @click.prevent="onClickMask">
    <div class="modal-wrap" @click.stop>
      <div class="modal-head">
        <button class="btn-close" @click.prevent="onClickClose">
          <span style="font-size: 20px;">&nbsp;X&nbsp;</span>
        </button>
      </div>
      <div class="modal-body"><slot/></div>
      <div class="modal-footer"></div>
    </div>
  </div>
</template>
<style>
* {
  margin: 0;
  padding: 0;
}
.modal-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: column;
  z-index: 100;
}
.modal-wrap {
  margin-top: 30px;
  width: 700px;
  max-width: 100vw;
  background-color: #fff;
  padding: 12px 12px 12px;
  border-radius: 6px;

  position: relative;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}
.modal-head {
  text-align: right;
}
.modal-body {
  overflow: auto;
  max-height: 90vh;
}
</style>

<script>
export default {
    name: 'WpModal',
    data(){
        return {
            isShow: false
        }
    },
    methods: {
        onClickMask(e) {
            // isShow = false;
            this.$emit('modalClose')
        },
        onClickClose() {
            // isShow = false;
            this.$emit('modalClose')
        }
    },
    mounted() {
        // if(this.$props.job) {
        //     this.job = this.$props.job;
        // }
        // console.log(this);
    },
    filters: {
        getPosition(str) {
            console.log('getPosition', str);
            // return str;
            if(typeof str === 'string'){
                const _arr = str.split('|') || [];
                const _result = _arr.map(_pos => (_pos === 'QLF_CD_005' ? '<strong class="sec-indigo4">치과의사</strong>' : _pos === 'QLF_CD_001' ? '<strong class="ter-blue3">치과위생사</strong>' : _pos === 'QLF_CD_002' ? '<strong class="ter-green1">간호조무사</strong>' : ''));
                console.log(_result);
                return _result.join('');
            } else {
                return '';
            }
        }
    }
}
</script>



